<template>
  <div class='mt-4'>
    <b-tabs
      content-class='searching-before-inputting_content'
      nav-class='searching-before-inputting_header'
      @input='getTabActive'
    >
      <div class='body_padding_inputting_content'>
        <div class='controller'>
          <div>
            <p>検索条件</p>
            <p v-if='!wrapperRequest.filterRequest.isEmptyString(displayConditionFilter)'
              class='font-weight-normal show_condition_filter'>
              {{ displayConditionFilter }}
            </p>
          </div>
          <div>
            <b-button variant='none' class='btn-white' @click='() => (isSearch=!isSearch)'>検索条件</b-button>
            <b-button variant='none' class='btn-white' @click='setDefaultConditionFilter'>検索条件リセット</b-button>
          </div>
        </div>
      </div>

      <b-tab active title-item-class='w_tab'>
        <template v-slot:title>
          進行中
          <br />
          ({{ totalElementsInProgress }})
        </template>

        <div v-if='tabActive === 0 && requestList.length > 0'>
          <request-display
            v-bind:request-list='requestList'
            v-bind:is-update-list='isUpdateList'
            v-on:set-is-update-list='setIsUpdateList'
          >
          </request-display>

          <b-pagination
            align='right'
            v-model='pageCurrent'
            :total-rows='totalElementsInProgress'
            :per-page='pageSize'
            class='pt-3 pr-5'
            @change='handlePage($event)'
          ></b-pagination>
        </div>
      </b-tab>

      <b-tab title-item-class='w_tab'>
        <template v-slot:title>
          すべて
          <br />
          ({{ totalElementsAll }})
        </template>

        <div v-if='tabActive === 1 && requestList.length > 0'>
          <request-display
            v-bind:request-list='requestList'
            v-bind:is-update-list='isUpdateList'
            v-on:set-is-update-list='setIsUpdateList'
          >
          </request-display>

          <b-pagination
            align='right'
            v-model='pageCurrent'
            :total-rows='totalElementsAll'
            :per-page='pageSize'
            class='pt-3 pr-5'
            @change='handlePage($event)'
          ></b-pagination>
        </div>
      </b-tab>

      <b-tab title-item-class='w_tab'>
        <template v-slot:title>
          差し戻し
          <br />
          ({{ totalElementsSendBack }})
        </template>

        <div v-if='tabActive === 2 && requestList.length > 0'>
          <request-display
            v-bind:request-list='requestList'
            v-bind:is-update-list='isUpdateList'
            v-on:set-is-update-list='setIsUpdateList'
          >
          </request-display>

          <b-pagination
            align='right'
            v-model='pageCurrent'
            :total-rows='totalElementsSendBack'
            :per-page='pageSize'
            class='pt-3 pr-5'
            @change='handlePage($event)'
          ></b-pagination>
        </div>
      </b-tab>

      <b-tab title-item-class='w_tab'>
        <template v-slot:title>
          却下
          <br />
          ({{ totalElementsReject }})
        </template>

        <div v-if='tabActive === 3 && requestList.length > 0'>
          <request-display
            v-bind:request-list='requestList'
            v-bind:is-update-list='isUpdateList'
            v-on:set-is-update-list='setIsUpdateList'
          >
          </request-display>

          <b-pagination
            align='right'
            v-model='pageCurrent'
            :total-rows='totalElementsReject'
            :per-page='pageSize'
            class='pt-3 pr-5'
            @change='handlePage($event)'
          ></b-pagination>
        </div>
      </b-tab>

      <template v-slot:tabs-end>
        <li role='presentation' class='nav-item select_filter'>
          <div class='filter'>
            <b-form inline>
              <b-select
                v-model='selectedSort'
                @change='goSelectedSort($event)'
                :options='sorts'
                value-field='name'
                text-field='name'
                class='mr-3'
              ></b-select>
              <b-select
                v-model='selectedLimit'
                @change='goSelectedLimit($event)'
                :options='pageLimit'
                value-field='limit'
                text-field='limit'
              ></b-select>
            </b-form>
          </div>
        </li>
      </template>
    </b-tabs>

    <request-search
      v-bind:is-search='isSearch'
      v-bind:tab-active='tabActive'
      v-bind:filter-request='wrapperRequest.filterRequest'
      v-on:set-is-search='setIsSearch'
      v-on:set-filter-request='setFilterRequest'
      v-on:set-filter-request-on-reset='setFilterRequestOnReset'
    >
    </request-search>
  </div>
</template>

<script>
import WrapperRequest from '@/models/WrapperRequest';
import FilterRequest from '@/models/FilterRequest';
import RequestService from '@/services/RequestService';
import RequestDisplay from '@/components/requests/RequestDisplay';
import RequestSearch from '@/components/popups/RequestSearch';

export default {
  name: 'RequestList',
  data() {
    return {
      tabActive: 0,
      pageCurrent: 1,
      pageSize: 20,
      isSearch: false,
      totalElementsInProgress: 0,
      totalElementsAll: 0,
      totalElementsSendBack: 0,
      totalElementsReject: 0,
      sorts: [
        {
          name: '申請日時(古い順)',
          field: 'requestedAt',
          orderBy: 'asc'
        },
        {
          name: '申請日時(新しい順)',
          field: 'requestedAt',
          orderBy: 'desc'
        },
        {
          name: '更新日時(古い順)',
          field: 'updatedStatusAt',
          orderBy: 'asc'
        },
        {
          name: '更新日時(新しい順)',
          field: 'updatedStatusAt',
          orderBy: 'desc'
        },
        {
          name: '金額(昇順)',
          field: 'billingAmount',
          orderBy: 'asc'
        },
        {
          name: '金額(降順)',
          field: 'billingAmount',
          orderBy: 'desc'
        }
      ],
      pageLimit: [
        {
          limit: 20
        },
        {
          limit: 50
        },
        {
          limit: 1
        },
        {
          limit: 4
        },
        {
          limit: 3
        }
      ],
      selectedSort: '申請日時(古い順)',
      selectedLimit: 20,
      wrapperRequest: new WrapperRequest(),
      requestList: [],
      displayConditionFilter: '',
      isUpdateList: false
    };
  },
  components: {
    RequestDisplay,
    RequestSearch
  },
  methods: {
    getTabActive(index) {
      this.tabActive = index;
      this.wrapperRequest = new WrapperRequest(index + 3, 0, 'requestedAt', 'asc', 20);
      this.wrapperRequest.filterRequest = new FilterRequest();
      this.selectedSort = '申請日時(古い順)';
      this.selectedLimit = 20;

      this.getRequestList(this.wrapperRequest);
    },
    goSelectedSort(name) {
      this.sorts.forEach(sort => {
        if (sort.name === name) {
          this.wrapperRequest.field = sort.field;
          this.wrapperRequest.orderBy = sort.orderBy;
          this.wrapperRequest.page = 0;

          this.getRequestList(this.wrapperRequest);
        }
      });
    },
    goSelectedLimit(limit) {
      this.wrapperRequest.limit = limit;
      this.wrapperRequest.page = 0;

      this.getRequestList(this.wrapperRequest);
    },
    setTotalElementsForTabActive(total) {
      switch (this.tabActive) {
        case 1:
          this.totalElementsAll = total;
          break;
        case 2:
          this.totalElementsSendBack = total;
          break;
        case 3:
          this.totalElementsReject = total;
          break;
        default:
          this.totalElementsInProgress = total;
      }
    },
    getRequestList(wrapperRequest) {
      RequestService.getRequestList(wrapperRequest)
        .then(res => {
          this.requestList = res.data.content;
          this.setTotalElementsForTabActive(res.data.totalElements);
          this.pageSize = res.data.pageable.pageSize;
          this.pageCurrent = res.data.number + 1;
          this.isUpdateList = true;
        })
        .catch(err => {
          alert('getRequestList: ' + err);
        });
    },
    handlePage(event) {
      this.wrapperRequest.page = event - 1;
      this.getRequestList(this.wrapperRequest);
    },
    setIsSearch(newValue) {
      this.isSearch = newValue;
    },
    setFilterRequest(newFilterRequest){
      this.wrapperRequest.page = 0;
      this.wrapperRequest.field = 'requestedAt';
      this.wrapperRequest.orderBy = 'asc';
      this.wrapperRequest.limit = 20;
      this.wrapperRequest.filterRequest = newFilterRequest;
      this.displayConditionFilter = newFilterRequest.toString();

      this.selectedSort = '申請日時(古い順)';
      this.selectedLimit = 20;

      this.getRequestList(this.wrapperRequest);
      this.isSearch = !this.isSearch;
    },
    setFilterRequestOnReset(newFilterRequest) {
      this.wrapperRequest.filterRequest = newFilterRequest;
    },
    setDefaultConditionFilter() {
      this.wrapperRequest.page = 0;
      this.wrapperRequest.field = 'requestedAt';
      this.wrapperRequest.orderBy = 'asc';
      this.wrapperRequest.limit = 20;
      this.wrapperRequest.filterRequest = new FilterRequest();
      this.displayConditionFilter = '';

      this.selectedSort = '申請日時(古い順)';
      this.selectedLimit = 20;

      this.getRequestList(this.wrapperRequest);
    },
    countRequestListDefault() {
      let promise = new RequestService.countRequestListDefault()
      .then(res => {
        this.totalElementsAll = res.data[0];
        this.totalElementsSendBack = res.data[1];
        this.totalElementsReject = res.data[2];
      })
      .catch(err => {
        this.$store.dispatch('AuthenticationModule/refreshToken')
        .then(() => {
          this.countRequestListDefault();
        })
        .catch(() => {
          // this.$router.push('/login');
        });
      })
    },
    setIsUpdateList(value) {
      this.isUpdateList = value;
    }
  },
  created() {
    this.countRequestListDefault();
  }
};
</script>

<style lang='scss'>
.controller {
  padding: 0rem 2rem;
  padding-top: 1rem;
  height: auto;

  .show_condition_filter {
    max-width: 40rem;
    font-size: 1.2rem;
  }
}

.table th,
.table td {
  border-top: 1px solid grey;
  font-size: 1rem;
  padding: 0.5rem;
  font-weight: 400;
}

.supplier_table {
  padding: 0 28% 0 10%;
  max-height: 25vh;
  overflow-y: scroll;
}
.table_requestList .vs__dropdown-toggle {
  min-height: 2.4rem;
}
.reuseIcon {
  font-size: 1.5rem;
  cursor: pointer;
}
.w_tab {
  //width: 9% !important;
  margin-right: 3%;
  &:last-child {
    margin-right: 0;
  }
}

.select_filter {
  width: 20rem !important;
  position: absolute;
  right: 0;
  bottom: 0;
  margin-bottom: 0 !important;
 
  .custom-select {
    border-width: 0;
    border-radius: 0;
    font-weight: 500;
    background-color: #E5E5E5 !important;
  }
}
</style>